// My Mixins
//
@mixin transform($x) {
  -webkit-transform : $x;
  -moz-transform : $x;
  -ms-transform : $x;
  -o-transform : $x;
  transform : $x;
}
@mixin transition($y){
  -webkit-transition: $y;
  -moz-transition: $y;
  -ms-transition: $y;
  -o-transition: $y;
  transition: $y;
}

@mixin hexaShadow($rule1, $rule2, $rule3, $rule4, $rule5, $rule6){
  -webkit-box-shadow: $rule1, $rule2, $rule3, $rule4, $rule5, $rule6;
  -moz-box-shadow: $rule1, $rule2, $rule3, $rule4, $rule5, $rule6;
  box-shadow: $rule1, $rule2, $rule3, $rule4, $rule5, $rule6;
}

@mixin opacityMix($numnum){
  /* IE 8 */
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity="#{$numnum*100}")";

  /* IE 5-7 */
  filter: alpha(opacity= $numnum*100);

  /* Netscape */
  -moz-opacity: $numnum;

  /* Safari 1.x */
  -khtml-opacity: $numnum;

  /* Good browsers */
  opacity: $numnum;
}
@mixin clear-grad($start, $percent1, $second, $percent2, $third, $percent3, $end, $percent4, $startHex, $endHex){

  background: -moz-linear-gradient(-45deg,  $start $percent1, $second $percent2, $third $percent3, $end $percent4);
  background: -webkit-gradient(linear, left top, right bottom, color-stop($percent1, $start, color-stop($percent2, $second), color-stop($percent3,$third), color-stop($percent4,$end)));
  background: -webkit-linear-gradient(-45deg,  $start $percent1, $second $percent2, $third $percent3,$end $percent4);
  background: -o-linear-gradient(-45deg,  $start $percent1, $second $percent2, $third $percent3,$end $percent4);
  background: -ms-linear-gradient(-45deg,  $start $percent1, $second $percent2, $third $percent3,$end $percent4);
  background: linear-gradient(135deg,  $start $percent1, $second $percent2, $third $percent3,$end $percent4);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#{$startHex}ff', endColorstr='#{$endHex}ff',GradientType=1 );
}

@mixin clear-grad-tint {
  background: -moz-linear-gradient(-45deg,  rgba(234,144,89,0.21) 0%, rgba(242,170,93,0.21) 11%, rgba(249,191,97,0.21) 20%, rgba(234,144,89,0.21) 36%, rgba(235,147,90,0.21) 38%, rgba(249,191,97,0.09) 63%, rgba(249,195,106,0.09) 64%, rgba(249,219,169,0.12) 71%, rgba(244,175,70,0.23) 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,rgba(234,144,89,0.21)), color-stop(11%,rgba(242,170,93,0.21)), color-stop(20%,rgba(249,191,97,0.21)), color-stop(36%,rgba(234,144,89,0.21)), color-stop(38%,rgba(235,147,90,0.21)), color-stop(63%,rgba(249,191,97,0.09)), color-stop(64%,rgba(249,195,106,0.09)), color-stop(71%,rgba(249,219,169,0.12)), color-stop(100%,rgba(244,175,70,0.23))); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(-45deg,  rgba(234,144,89,0.21) 0%,rgba(242,170,93,0.21) 11%,rgba(249,191,97,0.21) 20%,rgba(234,144,89,0.21) 36%,rgba(235,147,90,0.21) 38%,rgba(249,191,97,0.09) 63%,rgba(249,195,106,0.09) 64%,rgba(249,219,169,0.12) 71%,rgba(244,175,70,0.23) 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(-45deg,  rgba(234,144,89,0.21) 0%,rgba(242,170,93,0.21) 11%,rgba(249,191,97,0.21) 20%,rgba(234,144,89,0.21) 36%,rgba(235,147,90,0.21) 38%,rgba(249,191,97,0.09) 63%,rgba(249,195,106,0.09) 64%,rgba(249,219,169,0.12) 71%,rgba(244,175,70,0.23) 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(-45deg,  rgba(234,144,89,0.21) 0%,rgba(242,170,93,0.21) 11%,rgba(249,191,97,0.21) 20%,rgba(234,144,89,0.21) 36%,rgba(235,147,90,0.21) 38%,rgba(249,191,97,0.09) 63%,rgba(249,195,106,0.09) 64%,rgba(249,219,169,0.12) 71%,rgba(244,175,70,0.23) 100%); /* IE10+ */
  background: linear-gradient(135deg,  rgba(234,144,89,0.21) 0%,rgba(242,170,93,0.21) 11%,rgba(249,191,97,0.21) 20%,rgba(234,144,89,0.21) 36%,rgba(235,147,90,0.21) 38%,rgba(249,191,97,0.09) 63%,rgba(249,195,106,0.09) 64%,rgba(249,219,169,0.12) 71%,rgba(244,175,70,0.23) 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#36ea9059', endColorstr='#3bf4af46',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

@mixin gradient($one,$two,$three,$four) {
  background: $one; /* Old browsers */
  background: -moz-linear-gradient(-45deg,  $one 1%, $two 25%, $three 73%, $four 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right bottom, color-stop(1%,$one), color-stop(25%,$two), color-stop(73%,$three), color-stop(100%,#367d82)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(-45deg,  $one 1%,$two 25%,$three 73%,$four 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(-45deg,  $one 1%,$two 25%,$three 73%,$four 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(-45deg,  $one 1%,$two 25%,$three 73%,$four 100%); /* IE10+ */
  background: linear-gradient(135deg,  $one 1%,$two 25%,$three 73%,$four 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#{$one}', endColorstr='#{$four}',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

* {
  margin: 0;padding: 0;
  -webkit-box-sizing: border-box; /* Saf <5.1, */
  -moz-box-sizing: border-box; /* Fx */
  box-sizing: border-box; /* Ch, Saf 5.1+, IE 8+, Op 9+  */
}

/* Line Paper from Montana Flynn*/
@mixin line-paper($deg, $height) {
  background-image:
    -webkit-linear-gradient($deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    -webkit-linear-gradient(#eee .1em, transparent .1em);
  background-image:
    -moz-linear-gradient($deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    -moz-linear-gradient(#eee .1em, transparent .1em);
  background-image:
    -o-linear-gradient($deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    -o-linear-gradient(#eee .1em, transparent .1em);
  background-image:
    -ms-linear-gradient($deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    -ms-linear-gradient(#eee .1em, transparent .1em);
  -webkit-background-size: 100% $height;
  -moz-background-size: 100% $height;

  background-image:
    linear-gradient($deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    linear-gradient(#eee .1em, transparent .1em);
  background-size: 100% $height;
}


