#stayshine-content {
  width: 100%;
  height: 100%;
  min-height: 600px;
  background: $opWhiteIE;
  background-color: $opWhite;
  padding-bottom: 12px;
  margin: 20px auto 28px;
  overflow: hidden;

  // Fix for the on page links
  #top-of-web-design-page {
    display:block;padding:0;margin:0;height:0;width:0;position: relative;
    @media (max-width: 767px){
      top: -60px;
    }
    @media (min-width: 768px) {
      top: -82px;
    }
    @media (min-width: 1200px) {
      top: -92px;
    }

  }

  h2 {
    text-align: center;
    padding: .8em .2em .1em;
    font-size: 1.35em;
    line-height: 100%;

    b.headline {
      font-family: 'Arimo bold';
      text-decoration: none;
      color: #c1380d;
      text-shadow: 1px 1px 6px #444;

      @media (max-width: 579px){
        font-size: 82%;
        line-height: 80%;
      }
      @media (min-width: 580px) and (max-width: 767px){
        font-size: 98%;
        line-height: 100%;
      }
      @media (min-width: 768px) {
        font-size: 112%;
        line-height: 110%;
      }
      @media (min-width: 1200px) {
        font-size: 122%;
        line-height: 120%;
      }
    }
  }
  h3 {
    text-align: center;
    padding: .8em .2em .1em;
    font-size: 1.12em;

    b.headline {
      font-family: 'Arimo bold';
      font-size: 1.2em;
      text-decoration: none;
      color: #c1380d;
      text-shadow: -2px -1px 5px #333;
      text-shadow: -2px -1px 4px rgba(51, 51, 51, 0.42);

      @media (max-width: 579px){
        text-decoration: underline;
        font-size: 96%;
        line-height: 95%;
      }
      @media (min-width: 580px) and (max-width: 767px){
        font-size: 117%;
        line-height: 116%;
      }
      @media (min-width: 768px) {
        font-size: 128%;
        line-height: 126%;
      }
      @media (min-width: 1200px) {
        font-size: 133%;
        line-height: 131%;
      }
    }
  }
  h4 {
    text-align: center;
    font-size: 1.1em;
    line-height: 1.2;
    margin: 0;
    padding: 0.7em 0.6em 0.2em;
    background-color: rgba(255, 248, 175, .5);

    b.headline {
      text-decoration: none;
      color: #c1380d;

      @media (max-width: 579px){
        text-decoration: underline;
        font-size: 83%;
        line-height: 75%;
      }
      @media (min-width: 580px) and (max-width: 767px){
        font-size: 92%;
        line-height: 87%;
      }
      @media (min-width: 768px) {
        font-size: 105%;
        line-height: 100%;
      }
      @media (min-width: 1200px) {
        font-size: 112%;
        line-height: 107%;
      }

      &.blog {
        display: inline-block;
        background: #FFECBF;
        padding: .2em 3% 0;
        margin: 0;
      }
    }
  }

  .notebook {
    margin-top: 1.8em;
    background-color: #fff;
    @include line-paper(90deg, 1.2em);

    h4 {
      padding: .4em 0;
      margin: .4em auto;
    }
    h4:first-of-type {
      padding-top: 0;
      padding-bottom: .5em;
    }
    p {
      padding: 0.0em 2.7em;
      margin: 0.0em auto;
      line-height: 1.2em;
    }
  }
  p {
    padding: .2em 2em;
    font-size: 1em;
    text-indent: 1.6em;
  }
  ul li {
    font-size: 1em;
  }
  #slideshow-alt {
    display: none;
  }
  p.stayshine-recent-work {
    display: block;
    max-width: 42em;
  }
